<template>
  <table v-if="booksInCart.length > 0" class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">{{ $t("bookTitle") }}</th>
        <th scope="col">{{ $t("ISBN") }}</th>
        <th scope="col">{{ $t("price") }}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(book, index) in booksInCart" :key="book.ID">
        <th scope="row">{{ index + 1 }}</th>
        <td>{{ book.Name }}</td>
        <td>{{ book.ISBN }}</td>
        <td>{{ book.price }} €</td>
      </tr>
      <tr style="font-weight: bold; font-size: 1.2em; border-top: 1px solid">
        <td style="text-align: right" colspan="3">{{ $t("priceSum") }}</td>
        <td>{{ priceSum.toFixed(2) }} €</td>
      </tr>
      <tr style="font-weight: bold; font-size: 1.2em; border-top: 1px solid">
        <td style="text-align: right" colspan="4">
          <button @click.prevent="toPayment()" type="button" class="btn btn-success">
            {{ $t("toPayment") }}
          </button>
        </td>
      </tr>
    </tbody>
  </table>
  <div v-else style="text-align: center; width: 100%; color: rgba(95, 158, 160, 0.6)">
    <i
      style="font-size: 4em; margin-top: 25px; margin-bottom: 30px"
      class="fas fa-book"
    ></i>
    <h3>{{ $t("emptyCart") }}</h3>
  </div>
</template>

<script>
import BooksService from "../services/BooksService";

export default {
  name: "ShoppingCartPage",
  data() {
    return {
      booksInCart: [],
      priceSum: 0,
    };
  },
  async beforeCreate() {
    let inreachedBooksInCart = [];
    try {
      inreachedBooksInCart = await BooksService.getAllBooks();

      this.booksInCart = BooksService.getShoppingCartItems();

      inreachedBooksInCart = inreachedBooksInCart.filter(function (element) {
        return this.booksInCart.some((book) => book.bookID === element._id);
      }, this);

      this.booksInCart = inreachedBooksInCart;

      this.priceSum = this.booksInCart.reduce(function (accumulator, item) {
        return accumulator + parseFloat(item.price);
      }, 0);
    } catch (err) {
      this.error = err.message;
      console.error(err);
    }
  },
  methods: {
    toPayment() {
      this.$router.push("/payment");
    },
  },
};
</script>
